<template>
  <div class="page-wrapper">
    <div class="content container-fluid">
      <div class="row">
        <div class="col-lg-4">
          <div class="card">
            <div class="card-header">
              <h4 class="card-title text-center">Total Meal</h4>
            </div>
            <div class="card-body">
              <div class="card">
                <div class="card-body">
                  <div class="dash-widget-header">
                    <span class="dash-widget-icon text-danger border-danger">
                      <i class="fe fe-intersect"></i>
                    </span>
                    <div class="dash-count" v-for="item in total_meal" v-bind:key="item.total">
                      <h3>{{item.total}}</h3>
                    </div>
                  </div>
                  <div class="dash-widget-info">
                    <h6 class="text-muted">Total Meal</h6>
                    <div class="progress progress-sm">
                      <div class="progress-bar bg-danger w-100"></div>
                    </div>
                  </div>
                </div>
              </div>
            </div>
          </div>
        </div>
        <div class="col-lg-8">
          <div class="card">
            <div class="card-header pb-2">
              <div class="alert alert-secondary show" role="alert">
                <strong>Add</strong> Meal information
              </div>
            </div>
            <div class="card-body">
              <form @submit="submitData" method="post">
                <div class="form-group row">
                  <label class="col-lg-3 col-form-label">Select Member</label>
                  <div class="col-lg-9">
                    <select
                      class="form-control"
                      aria-label="Default select example"
                      v-model="post_meal.name"
                    >
                      <option disabled value="">Select Member</option>
                      <option v-for="mem in show_mem" v-bind:key="mem.id">
                        {{ mem.name }}
                      </option>
                    </select>
                  </div>
                </div>
                <div class="form-group row">
                  <label class="col-lg-3 col-form-label">Total Meal</label>
                  <div class="col-lg-9">
                    <input
                      type="number"
                      class="form-control"
                      min="1"
                      v-model="post_meal.no_meal"
                      required
                    />
                  </div>
                </div>
                <div class="form-group row">
                  <label class="col-lg-3 col-form-label">Date</label>
                  <div class="col-lg-9">
                    <input
                      type="date"
                      class="form-control"
                      v-model="post_meal.date"
                    />
                  </div>
                </div>
                <div class="text-right">
                  <button class="btn btn-outline-primary" type="submit">
                    Submit
                  </button>
                </div>
              </form>
            </div>
          </div>
        </div>
      </div>
      <div class="row">
        <div class="col-lg-12">
          <div class="card">
            <div class="card-header">
              <h4 class="card-title text-center">Meal History</h4>
            </div>
            <div class="card-body">
              <div class="table-responsive">
                <table
                  class="table table-bordered mb-0 table-hover text-center"
                >
                  <thead>
                    <tr>
                      <th>Name</th>
                      <th>Total Meal</th>
                      <th>Action</th>
                    </tr>
                  </thead>
                  <tbody v-if="show_meal.length != 0">
                    <tr v-for="item in show_meal" v-bind:key="item.id">
                      <td>{{ item.name }}</td>
                      <td>{{ item.total_meal }}</td>
                      <td>
                         <a
                          v-on:click="singleDetails(item.name)"
                          class="btn btn-sm bg-info-light"
                          data-toggle="modal"
                          data-target="#appt_details"
                        >
                          <i class="fa fa-eye"></i> View
                        </a>
                      </td>
                    </tr>
                  </tbody>
                </table>
              </div>
            </div>
          </div>
        </div>
      </div>
    </div>
     <div class="modal fade custom-modal" id="appt_details">
      <div
        class="modal-dialog modal-dialog-top modal-xl modal-dialog-scrollable"
      >
        <div class="modal-content">
          <div class="modal-header" style="border-bottom: 2px solid #80cc28">
            <h4 class="modal-title text-success">Meal Details</h4>
            <button
              type="button"
              class="close"
              data-dismiss="modal"
              aria-label="Close"
            >
              <span aria-hidden="true">&times;</span>
            </button>
          </div>
          <div class="modal-body">
            <div class="row">
              <div class="col-lg-12">
                <div class="card">
                  <div class="card-header">
                    <h4 class="card-title text-center">Meal Details</h4>
                  </div>
                  <div class="card-body">
                    <div class="table-responsive">
                      <table
                        class="
                          table table-bordered
                          mb-0
                          table-hover
                          text-center
                        "
                      >
                        <thead>
                          <tr>
                            <th>Name</th>
                            <th>No. Meal</th>
                            <th>Date</th>
                          </tr>
                        </thead>
                        <tbody v-if="meal_details.length != 0">
                          <tr  v-for="item in meal_details" v-bind:key="item.id">
                            <td>{{item.name}}</td>
                            <td>{{item.no_meal}}</td>
                            <td>{{item.date}}</td>
                          </tr>
                        </tbody>
                      </table>
                    </div>
                  </div>
                </div>
              </div>
            </div>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>
<script>
import axios from "axios";
import { useToast } from "vue-toastification";
import moment from "moment";
export default {
  data() {
    return {
      show_mem: [],
      show_meal: [],
      meal_details:[],
      total_meal:[],
      post_meal: {
        name: "",
        no_meal: "",
        date: moment().format("YYYY-MM-DD"),
      },
    };
  },

  setup() {
    const toast = useToast();
    return { toast };
  },

  methods: {
    submitData(e) {
      let formData = new FormData();
      for (let key in this.post_meal) {
        formData.append(key, this.post_meal[key]);
      }
      axios
        .post("http://localhost/meal_manager/api/manage_meal.php", formData)
        .then(() => {
          this.toast.success("Meal Added", {
            timeout: 1000,
            closeButton: false,
            hideProgressBar: true,
          });
          this.clearField();
          this.showMeal();
          this.show_Total();
        });
      e.preventDefault();
    },

    showMem() {
      axios
        .get("http://localhost/meal_manager/api/show_mem.php")
        .then((resp) => {
          this.show_mem = resp.data;
        });
    },

    showMeal(){
      axios
        .get("http://localhost/meal_manager/api/show_meal.php")
        .then((resp) => {
          this.show_meal = resp.data;
        });
    },
    show_Total() {
      axios
        .get("http://localhost/meal_manager/api/total_meal.php")
        .then((resp) => {
          this.total_meal = resp.data;
        });
    },


     singleDetails(name) { 
       axios.get("http://localhost/meal_manager/api/meal_details.php?name="+name).then((resp) => {
        this.meal_details = resp.data;
      });
     
    },


    clearField() {
      this.post_meal = {
        name: "",
        no_meal: null,
        date: moment().format("YYYY-MM-DD"),
      };
    },
  },

  mounted() {
    this.showMem();
    this.showMeal();
    this.show_Total();
    this.singleDetails();
    
  },
};
</script>