<template>
  <div class="page-wrapper">
    <div class="content container-fluid">
      <!-- Row -->
      <div class="row">
        <div class="col-sm-12">
          <!-- Validation Tooltips -->
          <div class="card">
            <div class="card-header">
              <h5 class="card-title text-success">Add Member</h5>
            </div>
            <div class="card-body">
              <form @submit="submitData" method="post">
                <div class="row">
                  <div class="col-md-6 col-sm-12">
                    <div class="form-group card-label">
                      <label>Name</label>
                      <input
                        class="form-control"
                        v-model="post.name"
                        type="text"
                      />
                    </div>
                  </div>
                  <div class="col-md-6 col-sm-12">
                    <div class="form-group card-label">
                      <label>Contact Number</label>
                      <input
                        class="form-control"
                        v-model="post.contact"
                        type="number"
                      />
                    </div>
                  </div>
                </div>
                <button type="submit" class="btn btn-outline-primary">
                  Submit
                </button>
              </form>
            </div>
          </div>
          <!-- /Validation Tooltips -->
        </div>
      </div>
      <div class="row">
        <div class="col-sm-12">
          <div class="card">
            <div class="card-body">
              <div class="table-responsive">
                <table class="table table-nowrap text-center">
                  <thead style="background-color: #5bc357; color: white">
                    <tr>
                      <th scope="col">ID</th>
                      <th scope="col">Full Name</th>
                      <th scope="col">Contact No.</th>
                      <th scope="col">Action</th>
                    </tr>
                  </thead>
                  <tbody v-if="mem_list.length != 0">
                    <tr  v-for="item in mem_list" v-bind:key="item.id">
                      <td>{{ item.id }}</td>
                      <td>{{ item.name }}</td>
                       <td>{{ item.contact}}</td>
                      <td>
                        <a class="btn btn-sm bg-danger-light ml-3"  v-on:click="deleteMem(item.id)">
                          <i class="fa fa-trash-o"></i> Delete
                        </a>
                      </td>
                    </tr>
                  </tbody>
                  <tbody v-else>
                    <tr>
                      <td colspan="4" class="text-center">No Data Found</td>
                    </tr>
                  </tbody>
                </table>
              </div>
            </div>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import axios from "axios";
import { useToast } from "vue-toastification";
export default {
  data() {
    return {
      mem_list:[],
      post: {
        name: "",
        contact: "",
      },
    };
  },
  setup() {
    const toast = useToast();
    return { toast };
  },
  methods: {
    submitData(e) {
      let formData = new FormData();
      for (let key in this.post) {
        formData.append(key, this.post[key]);
      }
      axios
        .post("http://localhost/meal_manager/api/member.php", formData)
        .then(() => {
          this.toast.success("Member Added", {
            timeout: 1000,
            closeButton: false,
            hideProgressBar: true,
          });
          this.clearField();
          this.showData();
        });
      e.preventDefault();
    },

    clearField(){
      this.post={
        name:null,
        contact:null,
      }
    },

    showData() {
      axios.get("http://localhost/meal_manager/api/show_mem.php").then((resp) => {
        this.mem_list = resp.data;
      });
    },
  deleteMem(id) {
      axios.delete("http://localhost/meal_manager/api/del_mem.php?id="+id).then(() => {
        this.toast.error("Member Deleted", {
            timeout: 1000,
            closeButton: false,
            hideProgressBar: true,
          });
        this.showData();
      });
      
    },
    
  },
   mounted() {
    this.showData();
  },
};
</script>

<style scoped>
.modal-body::-webkit-scrollbar {
  width: 10px;
  scroll-behavior: smooth;
}

.modal-body::-webkit-scrollbar-thumb {
  box-shadow: inset 0 0 6px rgb(84, 219, 72);
}
</style>