<template>
  <div class="page-wrapper">
    <div class="content container-fluid">
      <div class="row">
        <div class="col-sm-12">
          <div class="card">
            <div class="card-header">
               <span class="h4 card-title">Indivisual Report</span>
            </div>
            <div class="card-body">
              <div class="table-responsive">
                <table class="table table-nowrap text-center">
                  <thead style="background-color: #5bc357; color: white">
                    <tr>
                      <th scope="col">Name</th>
                      <th scope="col">Action</th>
                    </tr>
                  </thead>
                  <tbody v-if="final_report.length != 0">
                    <tr v-for="item in final_report" v-bind:key="item.name">
                      <td>{{ item.name }}</td>
                      <td>
                        <a
                          v-on:click="
                            viewMem(item.name);
                            viewHis(item.name);
                          "
                          class="btn btn-sm bg-info-light"
                          data-toggle="modal"
                          data-target="#appt_details"
                        >
                          <i class="fa fa-eye"></i> View
                        </a>
                      </td>
                    </tr>
                  </tbody>
                </table>
              </div>
            </div>
          </div>
        </div>
      </div>


      <div class="row" id="section-to-print">
        <div class="col-lg-12">
          <div class="card">
            <div class="card-header">
              <h4 class="upper-tab">
                <span class="h4 card-title">Meal Report</span>
                <span class="h4 card-title">{{ date }}</span>
              </h4>
            </div>
            <div class="card-body">
              <div class="table-responsive">
                <table
                  class="table table-bordered mb-0 table-hover text-center"
                >
                  <thead>
                    <tr>
                      <th>Name</th>
                      <th>Total Deposite</th>
                      <th>Total Meal</th>
                      <th>Meal Rate</th>
                      <th>Total Expenses</th>
                      <th>will Pay or Return</th>
                    </tr>
                  </thead>
                  <tbody v-if="final_report.length != 0">
                    <tr v-for="item in final_report" v-bind:key="item.name">
                      <td>{{ item.name }}</td>
                      <td>{{ item.td_amount }}</td>
                      <td>{{ item.total_meal }}</td>
                      <td>{{ item.meal_rate }}</td>
                      <td>{{ item.total_tk }}</td>
                      <td>{{ item.r_or_p }}</td>
                    </tr>
                  </tbody>
                  <tbody v-else>
                    <tr>
                      <td colspan="6" class="text-center">No Data Found</td>
                    </tr>
                  </tbody>
                </table>
              </div>
            </div>
          </div>
        </div>
      </div>
      <div class="row pb-5">
        <div class="col-lg-12">
          <center>
          <button @click="print" class="btn btn-danger">Print Report</button>
          </center>
       </div>
       </div>
    </div>

    <div class="modal fade custom-modal" id="appt_details">
      <div
        class="modal-dialog modal-dialog-top modal-xl modal-dialog-scrollable"
      >
        <div class="modal-content">
          <div class="modal-header" style="border-bottom: 2px solid #80cc28">
            <h4 class="modal-title text-success">Name Meal Details</h4>
            <button
              type="button"
              class="close"
              data-dismiss="modal"
              aria-label="Close"
            >
              <span aria-hidden="true">&times;</span>
            </button>
          </div>
          <div class="modal-body">
            <div class="row">
              <div class="col-lg-3">
                <div class="card pb-3">
                  <div class="card-header">
                    <h4 class="card-title text-center">Profile</h4>
                  </div>
                  <div class="card-body">
                    <div class="widget-profile pro-widget-content">
                      <div class="profile-info-widget">
                        <div class="upper-tab">
                          <a class="booking-doc-img">
                            <img
                              src="assets/img/profiles/mem_profile.png"
                              alt="User Image"
                              style="max-width: 80px"
                            />
                          </a>
                          <div
                            class="profile-det-info mt-4"
                            v-for="item in single_report"
                            v-bind:key="item.name"
                          >
                            <h3>{{ item.name }}</h3>
                          </div>
                        </div>
                      </div>
                    </div>
                  </div>
                </div>
              </div>
              <div class="col-lg-9">
                <div class="card booking-card">
                  <div class="card-header">
                    <h4 class="card-title text-center">Meal Info</h4>
                  </div>
                  <div class="card-body">
                    <!-- Booking Doctor Info -->
                    <div class="booking-doc-info">
                      <div class="alert alert-success fade show" role="alert">
                        <strong>Showing</strong> Last Updated Data
                      </div>
                    </div>
                    <!-- Booking Doctor Info -->

                    <div class="booking-summary">
                      <div class="booking-item-wrap">
                        <ul
                          class="booking-date"
                          v-for="item in single_report"
                          v-bind:key="item.name"
                        >
                          <li class="t2">
                            Total Meal<span class="t3">{{
                              item.total_meal
                            }}</span>
                          </li>
                          <li class="t2">
                            Meal Rate
                            <span class="t3">{{ item.meal_rate }} </span>
                          </li>
                        </ul>
                        <div class="booking-total">
                          <ul
                            class="booking-total-list"
                            v-for="item in single_report"
                            v-bind:key="item.name"
                          >
                            <li>
                              <span class="t1">Total Deposite</span>
                              <span class="total-cost"
                                >{{ item.td_amount }} </span
                              >
                            </li>
                          </ul>
                        </div>
                        <div class="booking-total" style="border-top: none">
                          <ul
                            class="booking-total-list"
                            v-for="item in single_report"
                            v-bind:key="item.name"
                          >
                            <li>
                              <span class="t1">Total Cost</span>
                              <span class="total-cost"
                                >{{ item.total_tk }} </span
                              >
                            </li>
                          </ul>
                        </div>
                        <div class="booking-total">
                          <ul
                            class="booking-total-list"
                            v-for="item in single_report"
                            v-bind:key="item.name"
                          >
                            <li>
                              <span class="t1">Will Pay or Return</span>
                              <span class="total-cost"
                                >{{ item.r_or_p }} </span
                              >
                            </li>
                          </ul>
                        </div>
                      </div>
                    </div>
                  </div>
                </div>
              </div>
             
            </div>
            <div class="row">
              <div class="col-lg-12">
                <div class="card">
                  <div class="card-header">
                    <h4 class="card-title text-center">Deposit History</h4>
                  </div>
                  <div class="card-body">
                    <div class="table-responsive">
                      <table
                        class="
                          table table-bordered
                          mb-0
                          table-hover
                          text-center
                        "
                      >
                        <thead>
                          <tr>
                            <th>Date</th>
                            <th>Amount</th>
                          </tr>
                        </thead>
                        <tbody v-if="sin_dep.length != 0">
                          <tr v-for="item in sin_dep" v-bind:key="item.name">
                            <td>{{ item.date }}</td>
                            <td>{{ item.amount }}</td>
                          </tr>
                        </tbody>
                        <tbody v-else>
                          <tr>
                            <td colspan="2" class="text-center">
                              No Data Found
                            </td>
                          </tr>
                        </tbody>
                      </table>
                    </div>
                  </div>
                </div>
              </div>
            </div>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>


<script>
import moment from "moment";
import axios from "axios";
export default {
  data() {
    return {
      final_report: [],
      single_report: [],
      sin_dep: [],
      date: moment().format("MMMM , YYYY"),
    };
  },

  methods: {
    print:function(){
      window.print()
    },
    finalReport() {
      axios
        .get("http://localhost/meal_manager/api/final_report.php")
        .then((resp) => {
          this.final_report = resp.data;
        });
    },
    viewMem(name) {
      axios
        .get("http://localhost/meal_manager/api/single_report.php?name=" + name)
        .then((resp) => {
          this.single_report = resp.data;
        });
    },

    viewHis(id) {
      axios
        .get("http://localhost/meal_manager/api/single_dep_his.php?name=" + id)
        .then((resp) => {
          this.sin_dep = resp.data;
        });
    },
  },
  mounted() {
    this.finalReport();
    this.viewHis();
    this.viewMem();
  },
};
</script>


<style >

@media print {
  body * {
    visibility: hidden;
  }
  #section-to-print, #section-to-print * {
    visibility: visible;
  }
  #section-to-print {
    position: absolute;
    left: 0;
    top: 0;
  }
}
.upper-tab {
  display: flex;
  justify-content: space-between;
  font-size: 15px;
  color: #799395;
}

.t1 {
  color: #799395;
  font-size: 15px;
}

.t2 {
  color: black;
  font-size: 18px;
}
.t3 {
  color: #337180;
  font-size: 18px;
}
</style>