<template>
  <div class="page-wrapper">
    <div class="content container-fluid">
      <div class="row">
        <div class="col-lg-4">
          <div class="card">
            <div class="card-header">
              <h4 class="card-title text-center">Total Costing</h4>
            </div>
            <div class="card-body">
              <div class="card">
                <div class="card-body">
                  <div class="dash-widget-header">
                    <span class="dash-widget-icon text-success">
                      <i class="fe fe-wallet"></i>
                    </span>
                    <div class="dash-count" v-for="item in total_expen" v-bind:key="item.total">
                      <h3>{{item.total}} </h3>
                    </div>
                  </div>
                  <div class="dash-widget-info">
                    <h6 class="text-muted">Total Expenses</h6>
                    <div class="progress progress-sm">
                      <div class="progress-bar bg-success w-100"></div>
                    </div>
                  </div>
                </div>
              </div>
            </div>
          </div>
        </div>
        <div class="col-lg-8">
          <div class="card">
            <div class="card-header pb-2">
              <div class="alert alert-secondary show" role="alert">
                <strong>Add</strong> Daily Expenses
              </div>
            </div>
            <div class="card-body">
              <form @submit="submitData" method="post">
                <div class="form-group row">
                  <label class="col-lg-3 col-form-label">Amount</label>
                  <div class="col-lg-9">
                    <input
                      type="number"
                      class="form-control"
                      min="1"
                      v-model="post_expenses.amount"
                    />
                  </div>
                </div>
                <div class="form-group row">
                  <label class="col-lg-3 col-form-label">Date</label>
                  <div class="col-lg-9">
                    <input
                      type="date"
                      class="form-control"
                      v-model="post_expenses.date"
                    />
                  </div>
                </div>
                <div class="text-right">
                  <button type="submit" class="btn btn-outline-primary">
                    Submit
                  </button>
                </div>
              </form>
            </div>
          </div>
        </div>
      </div>
      <div class="row">
        <div class="col-lg-12">
          <div class="card">
            <div class="card-header">
              <h4 class="card-title text-center">Expenses History</h4>
            </div>
            <div class="card-body">
              <div class="table-responsive">
                <table
                  class="table table-bordered mb-0 table-hover text-center"
                >
                  <thead>
                    <tr>
                      <th>Amount</th>
                      <th>Date</th>
                    </tr>
                  </thead>
                  <tbody v-if="expenses_his.length != 0">
                    <tr v-for="item in expenses_his" v-bind:key="item.id">
                      <td>{{item.amount}} </td>
                      <td>{{item.date}}</td>
                    </tr>
                  </tbody>
                  <tbody v-else>
                    <tr>
                      <td colspan="2" class="text-center">No Data Found</td>
                    </tr>
                  </tbody>
                </table>
              </div>
            </div>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>
<script>
import axios from "axios";
import { useToast } from "vue-toastification";
import moment from "moment";
export default {
  data() {
    return {
      post_expenses: {
        amount: "",
        date:moment().format("YYYY-MM-DD"),
      },
      expenses_his:[],
      total_expen:[],
    };
  },

   setup() {
    const toast = useToast();
    return { toast };
  },

  methods: {
    submitData(e) {
      let formData = new FormData();
      for (let key in this.post_expenses) {
        formData.append(key, this.post_expenses[key]);
      }
      axios
        .post("http://localhost/meal_manager/api/expenses.php", formData)
        .then(() => {
          this.toast.success("Expenses Added", {
            timeout: 1000,
            closeButton: false,
            hideProgressBar: true,
          });
          this.clearField();
          this.show_expen();
          this.show_total();
        });
      e.preventDefault();
    },
    clearField(){
      this.post_expenses={
        amount:null,
        date:moment().format("YYYY-MM-DD"),
      }
    },

    show_expen(){
       axios.get("http://localhost/meal_manager/api/show_expen.php").then((resp) => {
        this.expenses_his = resp.data;
      });
    },

    show_total(){
       axios.get("http://localhost/meal_manager/api/total_expen.php").then((resp) => {
        this.total_expen = resp.data;
      });
    },


  },

   mounted(){
    this.show_expen();
    this.show_total();
  },

};
</script>