<template>
  <div class="page-wrapper">
    <div class="content container-fluid">
      <div class="row">
        <div class="col-lg-4">
          <div class="card">
            <div class="card-header">
              <h4 class="card-title text-center">Deposite Balance</h4>
            </div>
            <div class="card-body">
              <div class="card">
                <div class="card-body">
                  <div class="dash-widget-header">
                    <span class="dash-widget-icon text-primary border-primary">
                      <i class="fe fe-money"></i>
                    </span>
                    <div class="dash-count" v-for="item in total_dep" v-bind:key="item.total">
                      <h3>{{item.total}} </h3>
                    </div>
                  </div>
                  <div class="dash-widget-info">
                    <h6 class="text-muted">Total Deposit</h6>
                    <div class="progress progress-sm">
                      <div class="progress-bar bg-primary w-100"></div>
                    </div>
                  </div>
                </div>
              </div>
            </div>
          </div>
        </div>
        <div class="col-lg-8">
          <div class="card">
            <div class="card-header pb-2">
                <div class="alert alert-secondary show" role="alert">
                  <strong>Please</strong> Add Balance to Meal Account
                </div>
            </div>
            <div class="card-body">
              <form @submit="submitData" method="post">
                <div class="form-group row">
                  <label class="col-lg-3 col-form-label">Select Member</label>
                  <div class="col-lg-9">
                    <select class="form-control" aria-label="Default select example" v-model="post_deposite.name">
                        <option disabled value="">Select Member</option>
                        <option v-for="mem in show_mem" v-bind:key="mem.id">
                          {{mem.name}}
                        </option>
                    </select>
                  </div>
                </div>
                <div class="form-group row">
                  <label class="col-lg-3 col-form-label">Amount</label>
                  <div class="col-lg-9">
                   <input type="number" class="form-control" min="0"  v-model="post_deposite.amount" required>
                  </div>
                </div>
                <div class="form-group row">
                  <label class="col-lg-3 col-form-label">Date</label>
                  <div class="col-lg-9">
                   <input type="date" class="form-control"  v-model="post_deposite.date" required>
                  </div>
                </div>
                <div class="text-right">
                 <button type="submit" class="btn btn-outline-primary">Submit</button>
                </div>
              </form>
            </div>
          </div>
        </div>
      </div>
      <div class="row">
        <div class="col-lg-12">
          <div class="card">
            <div class="card-header">
              <h4 class="card-title text-center">Deposite History</h4>
            </div>
            <div class="card-body">
              <div class="table-responsive">
                <table
                  class="table table-bordered mb-0 table-hover text-center"
                >
                  <thead>
                    <tr>
                      <th>Member Name</th>
                      <th>Amount</th>
                      <th>Date</th>
                    </tr>
                  </thead>
                  <tbody v-if="deposite_data.length != 0">
                    <tr v-for="item in deposite_data" v-bind:key="item.id">
                      <td>{{item.name}}</td>
                      <td>{{item.amount}} </td>
                      <td>{{item.date}}</td>
                     
                     
                     
                    </tr>
                  </tbody>
                  <tbody v-else>
                    <tr>
                      <td colspan="3" class="text-center">No Data Found</td>
                    </tr>
                  </tbody>
                </table>
              </div>
            </div>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import axios from "axios";
import { useToast } from "vue-toastification";
import moment from "moment";
export default {
  data() {
    return {
      deposite_data:[],
      show_mem:[],
      total_dep:[],
      post_deposite: {
        name: "",
        amount: "",
        date:moment().format("YYYY-MM-DD"),
      },
    };
  },
   setup() {
    const toast = useToast();
    return { toast };
  },
   methods: {
    submitData(e) {
      let formData = new FormData();
      for (let key in this.post_deposite) {
        formData.append(key, this.post_deposite[key]);
      }
      axios
        .post("http://localhost/meal_manager/api/deposite.php", formData)
        .then(() => {
          this.toast.success("Deposite Success", {
            timeout: 1000,
            closeButton: false,
            hideProgressBar: true,
          });
          this.clearField();
          this.showDep();
          this.show_total();
        });
      e.preventDefault();
    },

    clearField(){
      this.post_deposite={
        name:'',
        amount:'',
        date:moment().format("YYYY-MM-DD"),
      }
    },

    showMem() {
      axios.get("http://localhost/meal_manager/api/show_mem.php").then((resp) => {
        this.show_mem = resp.data;
      });
    },

    showDep() {
      axios.get("http://localhost/meal_manager/api/show_dep.php").then((resp) => {
        this.deposite_data = resp.data;
      });
    },
    show_total() {
      axios.get("http://localhost/meal_manager/api/total_dep.php").then((resp) => {
        this.total_dep = resp.data;
      });
    },
    
  },
   mounted() {
    this.showMem();
    this.showDep();
    this.show_total();
  },

};
</script>