<template>

 <div id="preloader" v-if="!isloaded">
        <div class="spinner"><img src="assets/img/logo.png"></div>
    </div>
</template>

<script>
  export default {
    data: () => {
      return {
        isloaded: false
      }
    },
    mounted() {
      document.onreadystatechange = () => {
        if (document.readyState == "complete") { 
          this.isloaded = true;
        } 
      }
    },
  }
</script>

<style scoped>
#preloader {
  background-color: #fff;
  width: 100%;
  height: 100%;
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  z-index: 5000;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-align: center;
  -ms-flex-align: center;
  -ms-grid-row-align: center;
  align-items: center;
  -webkit-box-pack: center;
  -ms-flex-pack: center;
  justify-content: center; }
  
  #preloader .spinner {
    margin: auto;
    animation: fadeInAnimation ease 2.5s;
    animation-iteration-count: infinite;
    animation-fill-mode: forwards;
     }

@keyframes fadeInAnimation {
    0% {
        opacity: 0.3;
    }
   
     50% {
        opacity:1;
     }
     100% {
        opacity:0.3;
     }
}

</style>