import { createApp } from 'vue'
import App from './App.vue'
import Toast from "vue-toastification";
import "vue-toastification/dist/index.css";
import {  createRouter, createWebHistory } from 'vue-router'
import Home from './components/Home.vue'
import Add_member from './components/Add_member.vue'
import Deposite from './components/Deposite.vue'
import Expenses from './components/Expenses.vue'
import Manage_meal from './components/Manage_meal.vue'
import Meal_report from './components/Meal_report.vue'



const routes = [
    { path: '/', name: 'Home', component: Home },
    { path: '/add_member', name: 'Add_member', component: Add_member },
    { path: '/deposite', name: 'Deposite', component: Deposite },
    { path: '/expenses', name: 'Expenses', component: Expenses },
    { path: '/manage_meal', name: 'Manage_meal', component: Manage_meal },
    { path: '/meal_report', name: 'Meal_report', component: Meal_report }


]



createApp(Add_member, Deposite, Expenses).use(Toast, {
    transition: "Vue-Toastification__fade",
});




const router = createRouter(
    {
       
        // base:'https://vue-meal.netlify.app/',
        routes,
        history:createWebHistory()
    }
)

createApp(App).use(router).mount('#app')