      <div class="row">
        <div class="col-md-12">
          <!-- Recent Orders -->
          <div class="card card-table">
            <div class="card-header mb-4">
              <h4 class="upper-tab">
                <span class="h4 card-title">Overall Report</span>
                <span class="h4 card-title">{{ date }}</span>
              </h4>
            </div>
            <div class="card-body">
              <div class="table-responsive">
                <table class="table table-stripped text-center">
                  <thead>
                    <tr>
                      <th>Name</th>
                      <th>Total Deposite</th>
                      <th>Total Meal</th>
                      <th>Meal Rate</th>
                      <th>Total Expenses</th>
                      <th>will Pay or Return</th>
                    </tr>
                  </thead>
                  <tbody v-if="final_report.length != 0">
                    <tr v-for="item in final_report" v-bind:key="item.name">
                      <td>{{ item.name }}</td>
                      <td>{{ item.td_amount }} </td>
                      <td>{{ item.total_meal }}</td>
                      <td>{{ item.meal_rate }} </td>
                      <td>{{ item.total_tk }} </td>
                      <td>{{ item.r_or_p }} </td>
                    </tr>
                  </tbody>
                  <tbody v-else>
                    <tr>
                      <td colspan="6" class="text-center">No Data Found</td>
                    </tr>
                  </tbody>
                </table>
              </div>
            </div>
          </div>
          <!-- /Recent Orders -->
        </div>
      </div>
    </div>
    <div class="row pb-5">
        <div class="col-lg-12">
          <center>
          <button @click="print" class="btn btn-danger">Print Report</button>
          </center>
       </div>
       </div>
  </div>
</template>
<script>
import moment from "moment";
import axios from "axios";

export default {
  data() {
    return {
      final_report: [],
      meal_dash: [],
      date: moment().format("MMMM , YYYY"),
    };
  },

  methods: {
    mealDash() {
      axios
        .get("http://localhost/meal_manager/api/meal_dash.php")
        .then((resp) => {
          this.meal_dash = resp.data;
        });
    },
      print(){
      window.print()
    },
    finalReport() {
      axios
        .get("http://localhost/meal_manager/api/final_report.php")
        .then((resp) => {
          this.final_report = resp.data;
        });
    },
  },
  mounted() {
    this.mealDash();
    this.finalReport();
  },
};
</script>


<style>
.upper-tab {
  display: flex;
  justify-content: space-between;
  font-size: 15px;
  color: #799395;
}


@media print {

 #app {
 padding:0;
 margin-left:-120px;}
 .page-header{
  display: none!important;
 }
  body * {
   visibility:hidden;
  }
  #section-to-print, #section-to-print * {
    visibility: visible;
  }
  #section-to-print {
    position: absolute;
    left: 0;
    top: 0;
    width:100%;
  }
}
</style>